import { useState } from "react";
import { ar, en } from "../configs/locales";
import { ar as exAr, en as exEn } from "../../../../data/locales";

const dict = { ar: { ...ar, ...exAr }, en: { ...en, ...exEn } };

/**
 */
export const useTranslation = ({ language, messages = {} } = {}) => {
  let storage = localStorage.getItem("lang");
  storage = storage == "undefined" ? undefined : storage;
  const [lang, set_lang] = useState(language ?? storage ?? "ar");

  const dictionary = {
    ar: { ...dict.ar, ...messages.ar },
    en: { ...dict.en, ...messages.en },
  };
  const setLang = (lang) => {
    const l = dictionary[lang];
    if (l) {
      localStorage.setItem("lang", lang);
      set_lang((l) => lang);
    }
    return l;
  };

  const t = (message) => {
    return dictionary[lang][message] ?? message;
  };

  return { t, setLang };
};
