import axios from "axios";
import jwtDefaultConfig from "./jwtDefaultConfig";

export default class JwtService {
  // ** jwtConfig <= Will be used by this service
    jwtConfig = { ...jwtDefaultConfig };
 
  // ** For Refreshing Token
  isAlreadyFetchingAccessToken = false;

  // ** For Refreshing Token
  subscribers = [];

  constructor(jwtOverrideConfig) {
    this.jwtConfig = { ...this.jwtConfig, ...jwtOverrideConfig };

    // ** Request Interceptor
    axios.interceptors.request.use(
      (config) => {
        // ** Get token from localStorage
        const accessToken = this.getToken();

        // ** If token is present add it to request's Authorization Header
        if (accessToken) {
          // ** eslint-disable-next-line no-param-reassign
          config.headers.Authorization = `${this.jwtConfig.tokenType} ${accessToken}`;
        }
        return config;
      },
      (error) => Promise.reject(error)
    );

    // ** Add request/response interceptor
    axios.interceptors.response.use(
      (response) => response,
      (error) => {
        // No JWT authentication is used
        // There are nighther refresh tokens nor authorization headers in our API
        return Promise.reject(error)

        // ** const { config, response: { status } } = error
        const { config, response } = error;
        const originalRequest = config;

        // ** if (status === 401) {
        if (response && response.status === 401) {
          if (!this.isAlreadyFetchingAccessToken) {
            this.isAlreadyFetchingAccessToken = true;
            this.refreshToken().then((r) => {
              this.isAlreadyFetchingAccessToken = false;

              // ** Update accessToken in localStorage
              this.setToken(r.data.accessToken);
              this.setRefreshToken(r.data.refreshToken);

              this.onAccessTokenFetched(r.data.accessToken);
            });
          }
          const retryOriginalRequest = new Promise((resolve) => {
            this.addSubscriber((accessToken) => {
              // ** Make sure to assign accessToken according to your response.
              // ** Check: https://pixinvent.ticksy.com/ticket/2413870
              // ** Change Authorization header
              originalRequest.headers.Authorization = `${this.jwtConfig.tokenType} ${accessToken}`;
              resolve(this.axios(originalRequest));
            });
          });
          return retryOriginalRequest;
        }
        return Promise.reject(error);
      }
    );
  }

  onAccessTokenFetched(accessToken) {
    this.subscribers = this.subscribers.filter((callback) =>
      callback(accessToken)
    );
  }

  addSubscriber(callback) {
    this.subscribers.push(callback);
  }

  getToken() {
    return localStorage.getItem(this.jwtConfig.storageTokenKeyName);
  }

  getRefreshToken() {
    return localStorage.getItem(this.jwtConfig.storageRefreshTokenKeyName);
  }

  setToken(value) {
    localStorage.setItem(this.jwtConfig.storageTokenKeyName, value);
  }

  setRefreshToken(value) {
    localStorage.setItem(this.jwtConfig.storageRefreshTokenKeyName, value);
  }
  sendEmail(...args) {
    return axios.post(args[0].sendEmailEndpoint+`?jwal=${args[0].jwal}` );
  }
  login(...args) {
    return axios.post(args[0].loginEndpoint,{
      company: args[0].companyId,
      identityfier: args[0].email,
      database: args[0].databaseId,
      apiKey: args[0].appKey,
      password: args[0].password,
      sendMessage:false,
      otp:"st"
      
     
    } );
  }
ConfirmCode(...args) {
    return axios.post(args[0].confirmCodeEndpoint
       , {
        company: args[0].companyId,
        identityfier: args[0].loginKey,
        database: args[0].databaseId,
        apiKey: args[0].appKey,
        password: args[0].phone,
        sendMessage:false,
        otp:args[0].otp
      // headers: {
      //     'Content-Type': 'application/json; charset=utf-8',
      //     'Accept': 'application/json'

      // }
});
  }


  newMYPassword(...args) {
    return axios.post(args[0].newMYPasswordEndpoint, {
      userID: args[0].userID,
      newPassword: args[0].newPassword,
      confirmNewPassword: args[0].confirmNewPassword,
      f_CompanyID: args[0].f_CompanyID 
    });
  }
  register(...args) {
    return axios.post(args[0].registerEndpoint, { 
     ...args[0].values,
      
    });
  } 

  refreshToken() {
    return axios.post(this.jwtConfig.refreshEndpoint, {
      refreshToken: this.getRefreshToken(),
    });
  }
}
