import { lazy } from "react";

const PagesRoutes = [
  {
    path: "/Landing",
    component: lazy(() =>
      import("../../views/pages/authentication/Landing/LandingPage")
    ),
    layout: "BlankLayout",
    meta: {
      authRoute: true,
    },
  },
  {
    path: "/index.html",
    component: lazy(() =>      
      import("../../views/pages/authentication/Landing/LandingPage")
    ),
    layout: "BlankLayout",
    meta: {
      publicRoute: true,
    },
  },
  {
    path: "/users",
    component: lazy(() => import("../../views/pages/SystemSetting/Users/List")),
  },
  {
    path: "/UserRoles",
    component: lazy(() =>
      import("../../views/pages/SystemSetting/UserRoles/List")
    ),
  },
  {
    path: "/roles",
    component: lazy(() => import("../../views/pages/SystemSetting/Roles/List")),
    exact: true,
  },
  {
    path: "/role/:id/Edit",
    component: lazy(() => import("../../views/pages/SystemSetting/Roles/Edit")),
    exact: true,
  },
  {
    path: "/rolepermission",
    component: lazy(() =>
      import("../../views/pages/SystemSetting/RolePermission/List")
    ),
    exact: true,
  },
  {
    path: "/login",
    component: lazy(() =>
      import("../../views/pages/authentication/Login")
    ),
    layout: "BlankLayout",
    meta: {
      authRoute: true,
    },
  },
  {
    path: "/Register",
    component: lazy(() =>
      import("../../views/pages/authentication/Register/index")
    ),
    layout: "BlankLayout",
    meta: {
      authRoute: true,
    },
  },
  {
    path: "/ForgotPassword",
    component: lazy(() =>
      import("../../views/pages/authentication/ForgotPassword")
    ),
    layout: "BlankLayout",
    meta: {
      authRoute: true,
    },
  },
  {
    path: "/ConfirmCode",
    component: lazy(() =>
      import("../../views/pages/authentication/ConfirmCode/index")
    ),
    layout: "BlankLayout",
    meta: {
      authRoute: true,
    },
  },
  {
    path: "/not-authorized",
    component: lazy(() => import("../../views/pages/misc/NotAuthorized")),
    layout: "BlankLayout",
    meta: {
      publicRoute: true,
    },
  },
  
  {
    path: "/Landing",
    component: lazy(() =>
      import("../../views/pages/authentication/Landing/Landing")
    ),
    layout: "BlankLayout",
    // meta: {
      // authRoute: true
    // }
  },
  {
    path: "/Home",
    component: lazy(() => import("../../views/pages/SystemSetting/Home")),
  },
   
  {
    path: "/country",
    component: lazy(() => import("../../views/pages/SystemSetting/Country/List")),
  },
  
  // {
  //   path: "/register",
  //   component: lazy(() =>
  //     import("../../views/pages/authentication/Register/index")
  //   ),
  //   layout: "BlankLayout",
  //   meta: {
  //     authRoute: true
  //   }
  // },

  
  
];

export default PagesRoutes;
