import { Fragment, useEffect } from "react";
import classnames from "classnames";
import { useSelector, useDispatch } from "react-redux";
import { useSkin } from "../../../../utility/hooks/useSkin";
import {
  handleContentWidth,
  handleMenuCollapsed,
  handleMenuHidden,
} from "../../../../redux/actions/layout";

const LayoutWrapper = (props) => {
  const { layout, children, appLayout, wrapperClass, transition, routeMeta } =
    props;

  const dispatch = useDispatch();
  
  // Select only necessary parts of the state
  const navbarStore = useSelector((state) => state.navbar);
  const contentWidth = useSelector((state) => state.layout.contentWidth);
  
  const [skin, setSkin] = useSkin();

  const Tag = layout === "HorizontalLayout" && !appLayout ? "div" : Fragment;

  const cleanUp = () => {
    if (routeMeta) {
      if (routeMeta.contentWidth) {
        dispatch(handleContentWidth("full"));
      }
      if (routeMeta.menuCollapsed) {
        dispatch(handleMenuCollapsed(!routeMeta.menuCollapsed));
      }
      if (routeMeta.menuHidden) {
        dispatch(handleMenuHidden(!routeMeta.menuHidden));
      }
    }
  };

  useEffect(() => {
    if (routeMeta) {
      if (routeMeta.contentWidth) {
        dispatch(handleContentWidth(routeMeta.contentWidth));
      }
      if (routeMeta.menuCollapsed) {
        dispatch(handleMenuCollapsed(routeMeta.menuCollapsed));
      }
      if (routeMeta.menuHidden) {
        dispatch(handleMenuHidden(routeMeta.menuHidden));
      }
    }
    return () => cleanUp();
  }, [routeMeta, dispatch]);

  return (
    <div
      className={classnames("app-content content overflow-hidden", {
        [wrapperClass]: wrapperClass,
        "show-overlay": navbarStore.query.length,
      })}
      style={{
        background: skin === "light" ? "#ffffff" : "#060d21",
      }}
    >
      <div className="content-overlay"></div>
      <div
        className="header-navbar-shadow"
        // style={{
        //   background: skin === "light" ? "#ffffff" : "#060d21",
        // }}
      />
      <div
        className={classnames({
          "content-wrapper": !appLayout,
          "content-area-wrapper": appLayout,
          "container p-0": contentWidth === "boxed",
          [`animate__animated animate__${transition}`]:
            transition !== "none" && transition.length,
        })}
      >
        <Tag
          {...(layout === "HorizontalLayout" && !appLayout
            ? { className: classnames({ "content-body": !appLayout }) }
            : {})}
        >
          {children}
        </Tag>
      </div>
    </div>
  );
};

export default LayoutWrapper;




