import { min } from "moment-timezone";
import { useState, useEffect } from "react";

new Date().getTime();

/**
 * Generates a timer object ticking every second
 *
 * @param {Date} startDate
 * @returns {Object}
 * @example const { seconds, minutes, hours, days } = useTimer(startDate,currentDate)
 */
export const useTimer = (startDate, currentDate) => {
  let errors = [];
  if (!startDate) {
    startDate = new Date();
    errors = [
      ...errors,
      "no start date ! ... set start date to " + startDate.toDateString(),
    ];
  }
  if (!currentDate) {
    currentDate = new Date();
    errors = [
      ...errors,
      "no current date ! ... set current date to " + currentDate.toDateString(),
    ];
  }
  if (startDate > currentDate) {
    const temp = startDate;
    startDate = currentDate;
    currentDate = temp;
    errors = [
      ...errors,
      "start date is after the current date !! ... swap the values of the dates",
    ];
  }
  const startTimestamp = startDate.getTime();
  const endTimestamp = currentDate.getTime();
  const [timer, setTimer] = useState(timeDiff(startTimestamp, endTimestamp));
  const [checkPointTime, setCheckPointTime] = useState(new Date().getTime());

  useEffect(() => {
    const timerId = setInterval(() => {
      const currentPoint = new Date().getTime();
      const diff = currentPoint - checkPointTime;
      setTimer(timeDiff(startTimestamp, endTimestamp, diff));
    }, 1000);

    return () => {
      clearInterval(timerId);
    };
  }, []);

  return {
    seconds: timer.s,
    minutes: timer.m,
    hours: timer.h,
    days: timer.d,
    errors,
  };
};

// let timestamp = Date.parse('11/23/2021 17:30:00');
export function timeDiff(startTimestamp, endTimestamp, add) {
  let diff = endTimestamp - startTimestamp + (add ?? 0);
  const days = Math.floor(diff / (24 * 60 * 60 * 1000));
  diff %= 24 * 60 * 60 * 1000;
  const hours = Math.floor(diff / (60 * 60 * 1000));
  diff %= 60 * 60 * 1000;
  const minutes = Math.floor(diff / (60 * 1000));
  diff %= 60 * 1000;
  const seconds = Math.floor(diff / 1000);
  return {
    d: days,
    h: hours,
    m: minutes,
    s: seconds,
  };
}
