/**
 * Converts the given string to PascalCase
 *
 * @param {String} s
 * @returns {String}
 */
export const toPascalCase = (s) => s.replace(
  /(\w)(\w*)/g,
  (g0, g1, g2) => { return g1.toUpperCase() + g2.toLowerCase() }
)
  .replace(/\-/g, '')
