const isNumeric = (n) => !isNaN(parseFloat(n)) && isFinite(n)

export const IconizedField = ({
  component,
  icons,
  paddingTop = 50,
  paddingStart = 5,
  paddingBottom,
  paddingEnd,
}) => {
  paddingTop = isNumeric(paddingTop) ? `${paddingTop}%` : paddingTop
  paddingStart = isNumeric(paddingStart) ? `${paddingStart}%` : paddingStart
  paddingBottom = isNumeric(paddingBottom) ? `${paddingBottom}%` : paddingBottom
  paddingEnd = isNumeric(paddingEnd) ? `${paddingEnd}%` : paddingEnd

  return (
    <>
      <div style={{ width: "100%", display: "stack", position: "relative" }}>
        {component}
        {icons?.map((icon, index) => {
          return (
            <div
              key={"icon_div_" + index}
              style={{
                position: "absolute",
                zIndex: "2",
                top: paddingTop,
                left: `${index * 7 + paddingStart}`,
                bottom: paddingBottom,
                right: `${index * 7 + paddingEnd}`,
              }}
            >
              {icon}
            </div>
          );
        })}
      </div>
    </>
  );
};
