// ** React Imports
import {
  Suspense,
  useContext,
  lazy,
  useEffect,
  useState,
  useMemo,
} from "react";
import { useDispatch, useSelector } from "react-redux";

// ** Utils
import { isScreenAllowed, isScreenNotAllowed, isUserLoggedIn } from "../utility/Utils";
import { useLayout } from "../utility/hooks/useLayout";
import { AbilityContext } from "../utility/context/Can";
import { useRouterTransition } from "../utility/hooks/useRouterTransition";

// ** Custom Components
// import Spinner from 'src/@core/components/spinner/Loading-spinner' // Uncomment if your require content fallback
import LayoutWrapper from "../@core/layouts/components/layout-wrapper";

// ** Router Components
import {
  BrowserRouter as AppRouter,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";

// ** Routes & Default Routes
import { DefaultRoute, Routes } from "./routes";

// ** Layouts
import BlankLayout from "../@core/layouts/BlankLayout";
import VerticalLayout from "../layouts/VerticalLayout";
import HorizontalLayout from "../layouts/HorizontalLayout";
import { getDefaultRoute } from "../utility/Utils";
import { useConfigs } from "../utility/hooks/useConfigs";
import { createListRequest } from "../utility/services/requests";
// import { getScreenGrantByUserRoleCodeRequestAsync } from "../redux/admin/screenGrant/thunk";

const Router = () => {
  const configs = useConfigs();
  if (!localStorage.getItem("lang") || !configs?.active_translation)
    localStorage.setItem("lang", configs?.default_language);
  if (
    !localStorage.getItem("skin") ||
    localStorage.getItem("skin") == "undefined" ||
    !configs?.active_theme
  )
    localStorage.setItem("skin", configs?.default_theme);

  // ** Hooks
  const [layout, setLayout] = useLayout();
  const [transition, setTransition] = useRouterTransition();
  const dispatch = useDispatch();
  const [HomeRoute, setHomeRoute] = useState(DefaultRoute);
  //  const screens = useSelector((state) => state.AdmRoutingScreenGrantReducer);
const [screens, setscreens] = useState([
 // {screenUrl : "/Complaints"}
]);
  useEffect(() => {
    
    // createListRequest({endPoint:"/AdmScreens/GetAll"})({
    //   setData:(d) => { 
    //   setscreens(d)
    // }});
  }, []) 
  // ** ACL Ability Context
  const ability = useContext(AbilityContext);

  // ** Default Layout
  const DefaultLayout =
    layout === "horizontal" ? "HorizontalLayout" : "VerticalLayout";
    
  // ** All of the available layouts
  const Layouts = { BlankLayout, VerticalLayout, HorizontalLayout };

  // ** Current Active Item
  const currentActiveItem = null;

  // ** Return Filtered Array of Routes & Paths
  const LayoutRoutesAndPaths = (layout) => {
    const LayoutRoutes = [];
    const LayoutPaths = [];

    if (Routes) {
      Routes.filter((route) => {
        /**
         * Disable the route if not authorized.
         * Permissions/authorization is applied in client side
                 */
         //   route.authorized = !isScreenNotAllowed(screens, route.path)
          route.authorized =  isScreenAllowed(screens, route.path)
          

        // ** Checks if Route layout or Default layout matches current layout
        if (
          route.layout === layout ||
          (route.layout === undefined && DefaultLayout === layout)
        ) {
          LayoutRoutes.push(route);
          LayoutPaths.push(route.path);
        }
      });
    }

    return { LayoutRoutes, LayoutPaths };
  };
  const NotAuthorized = lazy(() => import("../views/pages/misc/NotAuthorized"));

  // ** Init Error Component
  const Error = lazy(() => import("../views/pages/misc/Error"));

  // ** Set default homepage
  const defaultRoute = getDefaultRoute();
  if (HomeRoute != defaultRoute) setHomeRoute(defaultRoute);

  /**
   ** Final Route Component Checks for Login & User Role and then redirects to the route
   */
  const FinalRoute = (props) => {
    const route = props.route;
     let action, resource;
    // ** Assign vars based on route meta
    if (route.meta) {
      action = route.meta.action ? route.meta.action : null;
      resource = route.meta.resource ? route.meta.resource : null;
    }

    if (
      (!isUserLoggedIn() && route.meta === undefined) ||
      (!isUserLoggedIn() &&
        route.meta &&
        !route.meta.authRoute &&
        !route.meta.publicRoute)
    ) {
      /**
       ** If user is not Logged in & route meta is undefined
       ** OR
       ** If user is not Logged in & route.meta.authRoute, !route.meta.publicRoute are undefined
       ** Then redirect user to login
       */
      //return <Redirect to={defaultRoute} />;
      return <Redirect to="/Landing" />;
    } 
    else if (route.meta && route.meta.authRoute && isUserLoggedIn()) {
      // ** If route has meta and authRole and user is Logged in then redirect user to home page (DefaultRoute)
      return <Redirect to="/" />;
    } 
    // else if ( isUserLoggedIn() && !screens.some(action => action.screenUrl === route.path)) {
    //   console.log("User is logged in but route is not authorized:", route.path);
    //   console.log("Screens:", screens);
    //   // ** If user is Logged in and doesn't have ability to visit the page redirect the user to Not Authorized
    //   return <Redirect to="/not-authorized" />;
    // } 
    else {
      // ** If none of the above render component
      return <route.component {...props} />;
    }
  };

  // ** Return Route to Render
  const ResolveRoutes = () => {
    return Object.keys(Layouts).map((layout, index) => {
      // ** Convert Layout parameter to Layout Component
      // ? Note: make sure to keep layout and component name equal

      const LayoutTag = Layouts[layout];

      // ** Get Routes and Paths of the Layout
      const { LayoutRoutes, LayoutPaths } = LayoutRoutesAndPaths(layout);

      const routerProps = {};
      return (
        <Route path={LayoutPaths} key={index}>
          <LayoutTag
            routerProps={routerProps}
            layout={layout}
            setLayout={setLayout}
            transition={transition}
            setTransition={setTransition}
            currentActiveItem={currentActiveItem}
          >
            <Switch>
              {LayoutRoutes.map((route) => { 
                 
                return (
                  <Route
                    key={route.path}
                    path={route.path}
                    exact={route.exact === true}
                    render={(props) => {
                       Object.assign(routerProps, {
                        ...props,
                        meta: route.meta,
                      });
                      return (
                        <Suspense fallback={null}>
                           <LayoutWrapper
                            layout={DefaultLayout}
                            transition={transition}
                            setTransition={setTransition}
                             {...(route.appLayout
                              ? {
                                  appLayout: route.appLayout,
                                }
                              : {})}
                            {...(route.meta
                              ? {
                                  routeMeta: route.meta,
                                }
                              : {})}
                            {...(route.className
                              ? {
                                  wrapperClass: route.className,
                                }
                              : {})}
                           >
                             <FinalRoute route={route} {...props} /> 
                          </LayoutWrapper>
                        </Suspense>
                      );
                    }}
                  />
                );
              })}
            </Switch>
          </LayoutTag>
        </Route>
      );
    });
  };
  const basename = process.env.REACT_APP_BASENAME;
    return (
    <>
      {
        
        <AppRouter basename={basename}>
          <Switch>
             <Route
              exact
              path="/"
              render={() => {
                return isUserLoggedIn() ? (
                  <Redirect to={HomeRoute} />
                ) : (
                   <Redirect to="/Landing" />
                );
              }}
            />
             <Route
          exact
          path="/not_authorized"
          render={() => {
            return isUserLoggedIn() ? (
              <Redirect to="/not-authorized" />
            ) : (
              <Redirect to="/Landing" />
            );
          }}
        />
        {ResolveRoutes()} 
            <Route
              path="*" 
              render={() => {
                return isUserLoggedIn() ? (
                  <BlankLayout>
                    <Error />
                  </BlankLayout>
                ) : (
                  <Redirect to="/Landing" />
                );
              }}
            />
          </Switch>
        </AppRouter>
      }
    </>
  );
};

export default Router;



