export const getNavigation = (user) => {
  // return navigation.filter(
  //   (n) => !n.users || n.users.includes(user)
  // )
  return getNavigationFrom(navigation, user)
};

const getNavigationFrom = (list, user) => {
  return list.filter(
    (n) => !n.users || n.users.includes(user)
  ).map(n => {
    if (!n.children)
      return n
    else {
      n.children = getNavigationFrom(n.children, user)
      return n
    }
  })
}

