import apiClient from "../apiClient";
import { Local } from "../../../utility/page_storage";

const defaultBreaker = "BreAkeR";
const isBreakerFound = ({ breaker = defaultBreaker, params = {} }) => {
  // for (const ele of params) {
  //   if (ele === breaker) return true;
  // }
  // return false;
  for (const key in params) {
    if (Object.hasOwnProperty.call(params, key)) {
      if (params[key] === breaker) return true;
    }
  }
  return false;
  // }
  // Object.keys(params).map(key=>
  //   params[key].
  //   )
};
export const createListRequest =
  ({
    breaker = defaultBreaker,
    api = apiClient,
    endPoint,
    action = (_) => _,
    ifSuccess,
    ifFailure,
  } = {}) => !endPoint ? ()=>{} :
      (
       
        {
        id = "",
        params = {},
        onSuccess = (_) => _,
        onFailure = (_) => _,
        setData = (_) => _,
        setIsLoading = (_) => _,
        setError = (_) => _,
        dataAction = (_) => _,
      } = {}) => { 
        if (!(isBreakerFound({ breaker, params }) || breaker === id)) {
          setIsLoading(true);
           api.get(endPoint + id.toString(), { params })
            .then((response) => {
              let data = dataAction(response.data);
              data = action(data);
              setData(data); 
              setIsLoading(false); 
              if (ifSuccess) {
                ifSuccess(data);
              } else {
                onSuccess(data);
              }
            })
            .catch((err) => {
              if (err.response?.status == "401") {
                apiClient.removeSessionData();
                Local.refresh();
              }
              if (ifFailure) {
                ifFailure(err.response);
              } else {
                onFailure(err.response);
              }

              setIsLoading(false);
              setError(err.response);
            });
        }
      };

