// ** React Imports
import React from "react";
import { X } from "react-feather";
import { Modal, ModalHeader, ModalBody } from "reactstrap";

/**
 * @param minWidth the min width of the model according to the screen
 * @param maxWidth the max width of the model according to the screen
 * @param body the component as a body of the model
 * @param title the title of the model
 * @param handleModel open/close model function
 * 
 */
export const CustomModel = ({
  open=true,
  handleModel,
  title,
  body,
  maxWidth = "80%",
  minWidth = "10%",
}) => {
  const CloseBtn = (
    <X
      className="cursor-pointer"
      style={{ color: "#cc5555" }}
      size={20}
      onClick={handleModel}
    />
  );

  const cardTitle = title && <h2 style={{ textAlign: "center" }}> {title} </h2>;

  return (
    <Modal
      isOpen={open}
      toggle={handleModel}
      style={{ minWidth: minWidth, maxWidth: maxWidth }}
    >
      <ModalHeader
        className="mb-3"
        toggle={handleModel}
        close={CloseBtn}
        tag="div"
        // style={{backgroundColor:"white"}}
        
      >
        {cardTitle}
      </ModalHeader>
      <ModalBody  >
        <div style={{ padding: "5px" }}>{body}</div>
      </ModalBody>
    </Modal>
  );
};
