import { userRoles } from "./constants";

const storage = {
  userData: localStorage.getItem("userData"),
};

export const Local = {
  // window action
  refresh: () => window.location.reload(false),
  search: () => window.location.search,
  // local items action
  setItem: (item, value) => localStorage.setItem(item, value),
  getItem: (item) => localStorage.getItem(item),
  isItemEquals: (item, value) => localStorage.getItem(item) == value,
  removeItem: (item) => localStorage.removeItem(item),
  // user check
  isAdmin: () =>
    JSON.parse(localStorage.getItem("userType")) == userRoles.ADMIN,
  isUser: (userId) =>
    JSON.parse(localStorage.getItem("userData")) == userId,
  // local storage items
  userData: JSON.parse(storage.userData),
  userType: JSON.parse(storage.userData)?.userType,
  siteId: JSON.parse(storage.userData)?.siteId,
  /////*****///// another /////*****/////
  
  getSiteID: () => {
    return JSON.parse(storage.userData).siteId;
  },
  getSiteName: () => {
    return JSON.parse(storage.userData).siteName;
  },
};
